import axios from 'axios'
import { API_KEY, API_ENDPOINT } from './constants'

export const request = (method, query = null, data = null, rest = null ) => {
    const config = {
        method,
        url: query ? `${API_ENDPOINT}blogs?auth=${API_KEY}&where=${query}` : `${API_ENDPOINT}blogs?auth=${API_KEY}`,
        data: data,
        ...rest 
    }

    const promise = new Promise((resolve, reject) => {
        axios(config)
            .then(response => resolve(response.data))
            .catch(error => reject(error))
    })

    return promise
}