import {
	fetchPostsRequest,
	fetchPostsSuccess,
	fetchPostsError,
	fetchPostDetailRequest,
	fetchPostDetailSuccess,
	fetchPostDetailError
} from './actions'
import { request } from '../utils/request'

export const fetchPostAction = () => async (dispatch, getState) => {
	try {
		dispatch(fetchPostsRequest())
		request('GET')
			.then(res => {
				dispatch(fetchPostsSuccess(res))
			})
			.catch(err => dispatch(fetchPostsError(err)))
	} catch(err){
		dispatch(fetchPostsError(err))
	}
}

export const fetchPostDetailAction = slug => async (dispatch, getState) => {
	try {
		dispatch(fetchPostDetailRequest())
		request('GET', JSON.stringify({slug: slug}))
			.then(res => {
				dispatch(fetchPostDetailSuccess(res[0].fields))
			})
			.catch(err => dispatch(fetchPostDetailError(err)))
	} catch(err){
		dispatch(fetchPostDetailError(err))
	}
}