export const FETCH_POSTS_REQUEST = 'app/blog/FETCH_POSTS_REQUEST'
export const FETCH_POSTS_SUCCESS = 'app/blog/FETCH_POSTS_SUCCESS'
export const FETCH_POSTS_ERROR = 'app/blog/FETCH_POSTS_ERROR'
export const FETCH_POST_DETAIL_REQUEST = 'app/blog/FETCH_POST_DETAIL_REQUEST'
export const FETCH_POST_DETAIL_SUCCESS = 'app/blog/FETCH_POST_DETAIL_SUCCESS'
export const FETCH_POST_DETAIL_ERROR = 'app/blog/FETCH_POST_DETAIL_ERROR'

export const fetchPostsRequest = () => ({
	type: FETCH_POSTS_REQUEST
})

export const fetchPostsSuccess = posts => ({
	type: FETCH_POSTS_SUCCESS,
	payload: { posts }
})

export const fetchPostsError = message => ({
	type: FETCH_POSTS_ERROR,
	payload: { message }
})

export const fetchPostDetailRequest = () => ({
	type: FETCH_POST_DETAIL_REQUEST
})

export const fetchPostDetailSuccess = post => ({
	type: FETCH_POST_DETAIL_SUCCESS,
	payload: { post }
})

export const fetchPostDetailError = message => ({
	type: FETCH_POST_DETAIL_ERROR,
	payload: { message }
})