import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { DiscussionEmbed } from 'disqus-react'
import { request } from '../utils/request'
import Layout from '../components/Layout'
import { imgParser } from '../utils/helpers'
import { fetchPostDetailAction } from '../redux/thunks'
import NotFoundLogo from '../assets/images/error-404.svg'
import Article from '../assets/images/article.svg'
import moment from 'moment'

const Detail = ({ match, onFetchDetail, postDetail, request }) => {
	const { slug } = match.params
	const [detail, setDetail] = useState(new Map())

	useEffect(() => {
		onFetchDetail(slug)
	}, [slug])

	useEffect(() => {
		setDetail(postDetail)
	}, [postDetail])

	return (
		<Layout title={detail.get('title')}>
			{ request.success && 
				<section className="text-gray-600 body-font">
				  <div className="container px-5 py-24 mx-auto w-4/6">
				    <div className="flex flex-wrap w-full mb-20">
				      <div className="w-full mb-6 lg:mb-0">
				        <h1 className="sm:text-3xl text-2xl font-medium title-font mb-2 text-gray-900">{detail.get('title')}</h1>
				        <div className="h-1 w-20 bg-indigo-500 rounded"></div>
				        <div className="mt-8">
				        	<span className="text-gray-400 pr-3 mr-3 border-r-2 border-gray-200 uppercase text-sm title-font font-medium">{moment(detail.get('createdAt')).format('DD MMM YYYY')}</span> 
				        	<span className="text-gray-400 pr-3 mr-3 border-r-2 border-gray-200 uppercase text-sm title-font font-medium">{detail.get('cat')}</span>
				        	<span className="text-gray-400 uppercase text-sm title-font font-medium">{detail.get('postedBy')}</span>
				        </div>
				      </div>
				    </div>
				    <div class="flex items-center justify-center flex-col mb-20">
						<img loading="lazy" src={detail.get('cover')} className="object-cover object-center h-full w-full border border-gray-200 rounded"/>
					</div>
				    <div className="my-4" dangerouslySetInnerHTML={{__html: imgParser(detail.get('content'))}}></div>
				  </div>
				</section>
			}

			{ request.loading && 
				<section className="flex items-center justify-center flex-col w-full py-24 mt-20">
					<img className="lg:w-1/6 md:w-2/6 w-4/6 mb-10 object-cover object-center rounded" alt="not found" src={Article}/>
		     		<h2 className="title-font sm:text-3xl text-2xl mb-10 font-medium text-gray-900">Memuat...</h2>
		     	</section>
			}

			{ request.error && 
				<section className="flex items-center justify-center flex-col w-full py-24 mt-20">
					<img className="lg:w-1/6 md:w-2/6 w-4/6 mb-10 object-cover object-center rounded" alt="not found" src={NotFoundLogo}/>
		     		<h2 className="title-font sm:text-3xl text-2xl mb-10 font-medium text-gray-900">Artikel tidak ditemukan!</h2>
		     	</section>
			}
		</Layout>
	)
}

const mapStateToProps = state => ({
	postDetail: state.get('postDetail'),
	request: {
		success: state.get('success'),
		loading: state.get('loading'),
		error: state.get('error')
	}
})

const mapDispatchToProps = dispatch => ({
	onFetchDetail: slug => dispatch(fetchPostDetailAction(slug))
})

export default connect(mapStateToProps, mapDispatchToProps)(Detail)