import React, { useState, useEffect } from 'react'
import GridItem from './GridItem'
import NoPosts from '../../assets/images/content-man.svg'


const GridView = ({ posts }) => {
	const [cat, setCat] = useState('all')
	const [items, setItems] = useState([])

	const active = 'text-indigo-500 border-indigo-500'
	const notActive = 'border-gray-300'
	const linkClass = 'flex-grow border-b-2 py-2 text-lg px-1 text-center cursor-pointer font-semibold '
	const category = [
		{ slug: 'all', title: 'ALL' },
		{ slug: 'react', title: 'REACT' },
		{ slug: 'firebase', title: 'FIREBASE' },
		{ slug: 'nodejs', title: 'NODEJS' },
	]

	useEffect(() => {
		setItems(posts)
	}, [posts])

	useEffect(() => {
		cat === 'all'
			? setItems(posts)
			: setItems(posts.filter(post => post.getIn(['fields', 'cat']) === cat))
	}, [cat])

	return (
		<section className="text-gray-600 body-font">
		  <div className="container px-5 py-20 mx-auto">
		  	<div className="flex mb-10 w-2/6 items-center justify-center mx-auto">
		  		{ category.map((cate, i) => (
	          		<span 
	          			key={i} 
	          			onClick={() => setCat(cate.slug)} 
	          			className={linkClass + (cate.slug === cat ? active : notActive) }
	          		>
	          			{cate.title}
	          		</span>
		  		))}
	        </div>
		    <div className="flex flex-wrap -m-4">
		     	{ items.filter(item => item.getIn(['fields', 'status']) === 'published').map((post, i) => (
		      		<GridItem key={i} post={post.get('fields')} />
		     	))}

		     	{ items.size === 0 && 
		     		<div className="flex items-center justify-center flex-col w-full py-20">
		     			<img className="lg:w-1/6 md:w-2/6 w-4/6 mb-10 object-cover object-center rounded" alt="not found" src={NoPosts}/>
		     			<h2 className="title-font sm:text-3xl text-2xl mb-10 font-medium text-gray-900">No posts</h2>
		     		</div>
		     	}
		    </div>
		  </div>
		</section>
	)
}

export default GridView