import { fromJS } from 'immutable'
import {
	FETCH_POSTS_REQUEST,
	FETCH_POSTS_SUCCESS,
	FETCH_POSTS_ERROR,
	FETCH_POST_DETAIL_REQUEST,
	FETCH_POST_DETAIL_SUCCESS,
	FETCH_POST_DETAIL_ERROR
} from './actions'

const initState = fromJS({
	loading: false,
    error: false,
    success: false,
    message: null,
    posts: [],
    postDetail: {}
})

const blogReducer = (state = initState, action) => {
	const { type, payload } = action

	switch(type){
		case FETCH_POSTS_REQUEST:
		case FETCH_POST_DETAIL_REQUEST: {
			return state
                .set('loading', true)
                .set('error', false)
                .set('success', false)
                .set('message', null)
                .set('postDetail', fromJS({}))
		}

		case FETCH_POSTS_SUCCESS: {
			const { posts } = payload

			return state
                .set('loading', false)
                .set('success', true)
                .set('posts', fromJS(posts))
		}

		case FETCH_POST_DETAIL_SUCCESS: {
			const { post } = payload

			return state
                .set('loading', false)
                .set('success', true)
                .set('postDetail', fromJS(post))
		}

		case FETCH_POSTS_ERROR:
		case FETCH_POST_DETAIL_ERROR: {
			const { message } = payload

			return state
				.set('loading', false)
				.set('error', true)
				.set('message', message)
				.set('postDetail', fromJS({}))
		}

		default:
            return state
	}
}

export default blogReducer